import React from 'react';
import Particles from 'react-particles-js';
import settings from './settings.json';

export default class ParticleWave extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      vpWidth: null
    };

    this.updateWindowDimensions = this.updateWindowDimensions.bind(this);
  }

  componentDidMount() {
    this.updateWindowDimensions();
  }

  updateWindowDimensions() {
    this.setState({ vpWidth: window.innerWidth });
  }

  render() {
    const mobileStyles = {
      height: '40vh',
      animation: 'none'
    };

    const desktopStyles = {
      willChange: 'transform',
      animation: this.props.static ? 'none' : 'wave 10s infinite alternate'
    };

    return (
      <div className="particles__wrapper">
        <Particles
          params={{
            particles: {
              number: {
                value: this.state.vpWidth > 1024 ? 50 : 100,
                density: {
                  enable: false
                }
              },
              color: {
                value: '#25dbe2'
              },
              shape: {
                type: 'circle',
                stroke: {
                  width: 0,
                  color: '#000000'
                },
                polygon: {
                  nb_sides: 8
                }
              },
              opacity: {
                value: 1,
                random: true
              },
              size: {
                value: 5,
                random: true,
                anim: {
                  enable: true,
                  speed: 40,
                  size_min: 0.1,
                  sync: false
                }
              },
              line_linked: {
                enable: true,
                distance: 150,
                color: '#25dbe2',
                opacity: 1,
                width: 2
              },
              move: {
                enable: true,
                speed: 2,
                direction: 'top',
                random: true,
                straight: true,
                out_mode: 'bounce',
                bounce: true
              }
            },
            interactivity: {
              detect_on: 'canvas',
              events: {
                onhover: {
                  enable: true,
                  mode: 'grab'
                },
                resize: true
              },
              modes: {
                grab: {
                  distance: 200,
                  line_linked: {
                    opacity: 1
                  }
                }
              }
            },
            retina_detect: true
          }}
          style={this.state.vpWidth > 1024 ? mobileStyles : mobileStyles}
        />

        <style jsx global>{`
          .particles__wrapper {
            position: absolute;
            bottom: 0;
            width: 100%;
            height: 40vh;
          }
          .particles__wrapper > div {
            height: 40vh;
            opacity: 0.6;
          }
        `}</style>
      </div>
    );
  }
}
